.modal {
    width: 100%;
    height: 100%;
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.5); 
    justify-content: center;
    align-items: center;
    z-index: 1000;
}
.modal.open {
    display: block;
}


.modal-container {
    width: 700px;
    height: auto;
    position: relative;
    background-color:#C8C8C8 ;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    margin: 0 auto; 
}


.modal-title{
    color: #DB202C;
}

.modal-btn {
    width: 70px;
    background-color: #DB202C;
    color: white;
    border: 0;
    padding: .3rem;
    position: absolute;
    bottom: 20px;
    right: 40px;
    font-size: 20px;
    cursor: pointer;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
}


    